<template>
  <div>
    {{ longTxtToDisplay }}
    <button
      v-if="txt.length > 100"
      class="expend-description"
      @click="onReadMore"
    >
      {{isLongTxtShown ? "Less" : "Show More" }}
    </button>
  </div>
</template>

<script>
export default {
  props: ["txt"],
  data() {
    return {
      isLongTxtShown: false,
    };
  },
  methods: {
    onReadMore() {
      this.isLongTxtShown = !this.isLongTxtShown;
    },
  },
  computed: {
    longTxtToDisplay() {
      if (this.txt.length < 100 || (this.isLongTxtShown && this.txt.length > 100))
        return this.txt;
      else {
        return this.txt.substring(0, 100) + " . . .";
      }
    },
  },
};
</script>
