const jsonBooks = require('../books.json')
import axios from 'axios'
import { storageService } from './storageService';
import { utilsService } from './utilsService'

export const bookService = {
    query,
    save,
    getById,
    remove,
    saveReview,
    removeReview,
    sumReviewsAvg,
    getBooksApi,
    addGoogleBook,
    getNextPrev
}

var gBooks;

async function query() {
    gBooks = storageService.load('gBooks')
    if (!gBooks) {
        gBooks = jsonBooks

        gBooks.forEach(book => {
            return book.reviews = []
        })
        storageService.save('gBooks', gBooks)
    }
    return gBooks
}

async function save(newBook) {
    try {
        if (newBook.id) {
            const currBookIdx = gBooks.findIndex(book => book.id === newBook.id)
            gBooks[currBookIdx] = newBook
        }
        else {
            gBooks.push(newBook)
        }
    }
    catch (err) {
        console.log('Found problem in save-book service,', err);
    }
    finally {
        storageService.save('gBooks', gBooks)
    }
}

async function getById(id) {
    if (!gBooks) query()
    const book = await gBooks.find(book => book.id === id)
    return book
}
function getIdx(id) {
    return gBooks.findIndex(book => book.id === id)
}

async function remove(id) {
    try {

        const idx = gBooks.findIndex(book => book.id === id)
        gBooks.splice(idx, 1)
    }
    catch (err) {
        console.log('Error in remove-book service,', err);
    }
    finally {
        storageService.save('gBooks', gBooks)
    }
}
async function getNextPrev(bookId, direction) {
    const currIdx = await getIdx(bookId)
    if (direction === 'next') {
        return (currIdx + 1 === gBooks.length) ? gBooks[0] : gBooks[currIdx + 1]
    }
    else {
        return (currIdx - 1 === -1) ? gBooks[gBooks.length - 1] : gBooks[currIdx - 1]
    }
}

async function saveReview(review, bookId) {
    const bookIdx = getIdx(bookId)
    const book = gBooks[bookIdx]
    if (!book.reviews) {
        book.reviews = []
    }
    review.id = utilsService.makeId()
    book.reviews.push(review)
    storageService.save('gBooks', gBooks)

}

async function removeReview(reviewId, bookId) {
    const bookIdx = getIdx(bookId)
    const reviewIdx = gBooks[bookIdx].reviews.findIndex(review => review.id === reviewId)
    gBooks[bookIdx].reviews.splice(reviewIdx, 1)
    storageService.save('gBooks', gBooks)

}

function sumReviewsAvg(reviews) {
    const rating = reviews.reduce((acc, currRev) => {
        if (reviews.length) acc = acc + +currRev.rating;
        else acc = 0;
        return acc;
    }, 0);
    return rating ? (rating / reviews.length).toFixed(1) : 0;
}

async function getBooksApi(params) {
    const res = await axios.get(`https://www.googleapis.com/books/v1/volumes?printType=books&q=${params}`)
    return res.data.items
}

async function addGoogleBook(googleBook) {
    let book = await _formatGoogleBook(googleBook)
    gBooks.unshift(book)
    storageService.save('gBooks', gBooks)
    return book
}


async function _formatGoogleBook(book) {
    return {
        id: book.id,
        reviews: [],
        title: book.volumeInfo.title,
        subtitle: book.volumeInfo.subtitle,
        authors: [
            book.volumeInfo.authors
        ],
        publishedDate: book.volumeInfo.publishedDate,
        description: book.volumeInfo.description || '',
        pageCount: book.volumeInfo.pageCount,
        categories: [
            book.volumeInfo.categories
        ],
        thumbnail: book.volumeInfo.imageLinks.thumbnail,
        language: book.volumeInfo.language,
        listPrice: {
            amount: book.saleInfo.listPrice ? book.saleInfo.listPrice.amount : 0,
            currencyCode: book.saleInfo.listPrice ? book.saleInfo.listPrice.currencyCode : '',
            isOnSale: book.saleInfo.saleability === 'FOR_SALE' ? true : false,
        }
    }
}