<template>
  <section >
    <div :class="[`filter flex`, {'show-filter':isFilterOpen}]">
      <img
        src="https://res.cloudinary.com/idanrozen/image/upload/v1601236322/search_ejr7qx.png"
        alt=""
      />
      <input
        v-model="filterBy.title"
        autoComplete="off"
        placeholder="Search book"
        class="styled-input"
        @input="filter"
      />
      <input
        v-model.number="filterBy.minPrice"
        placeholder="By Min-Price"
        type="number"
        class="styled-input"
        min="1"
        @input="filter"
      />
       <input
        v-model.number="filterBy.maxPrice"
        placeholder="By Max-Price"
        type="number"
        class="styled-input"
        min="1"
        @input="filter"
      />
    </div>
  </section>
</template>

<script>
export default {
  props:["isFilterOpen"],
  data() {
    return {
      filterBy: {
        title: "",
        minPrice: -Infinity,
        maxPrice: Infinity,
      },
    };
  },
  methods: {
    filter() {
      this.$emit("filtered", this.filterBy);
    },
  },
};
</script>

