<template>
  <section class="main-container">
    <AddReview @onAddReview="addReview" :isAddReview="isAddReview" />
    <ReviewList :reviews="reviews" @removeReview="removeReview" />
  </section>
</template>

<script>
import ReviewList from "./ReviewList.vue";
import AddReview from "./AddReview.vue";
import { bookService } from "../services/bookService";
import  eventBus  from "../services/eventBus";
export default {
  props: ["bookId", "reviews", "isAddReview"],
  methods: {
    async addReview(review) {
      await bookService.saveReview(review, this.bookId);
      this.$emit('closeAdd')
      eventBus.$emit("showMsg", {
        txt: "Saved Successfully",
        type: "success",
      });
    },
    async removeReview(reviewId) {
      await bookService.removeReview(reviewId, this.bookId);
      eventBus.$emit("showMsg", {
        txt: "Removed Successfully",
        type: "success",
      });
    },
  },
  components: { AddReview, ReviewList },
};
</script>

