<template>
  <section>
    <i class="fas fa-palette" @click.prevent="openPalette"></i>

    <div class="colors-container flex" v-if="isPaletteShown" @click.prevent="">
      <button
        @click.prevent="onChangeColor($event, '#fff4755c')"
        :style="{ backgroundColor: '#fff4755c' }"
      ></button>
      <button
        @click.prevent="onChangeColor($event, '#ffc10752')"
        :style="{ backgroundColor: '#ffc10752' }"
      ></button>
      <button
        @click.prevent="onChangeColor($event, '#8bccff42')"
        :style="{ backgroundColor: '#8bccff42' }"
      ></button>
      <button
        @click.prevent="onChangeColor($event, '#d7aefb4a')"
        :style="{ backgroundColor: '#d7aefb4a' }"
      ></button>
      <button
        @click.prevent="onChangeColor($event, '#c4fd8254')"
        :style="{ backgroundColor: '#c4fd8254' }"
      ></button>
      <button
        @click.prevent="onChangeColor($event, '#f28b824a')"
        :style="{ backgroundColor: '#f28b824a' }"
      ></button>
      <button
        @click.prevent="onChangeColor($event, '#3f51b52b')"
        :style="{ backgroundColor: '#3f51b52b' }"
      ></button>
      <button
        @click.prevent="onChangeColor($event, '#69c4d83d')"
        :style="{ backgroundColor: '#69c4d83d' }"
      ></button>
      <button
        @click.prevent="onChangeColor($event, '#e8eaed')"
        :style="{ backgroundColor: '#e8eaed' }"
      ></button>
      <button
        @click.prevent="onChangeColor($event, '#a7ffeb63')"
        :style="{ backgroundColor: '#a7ffeb63' }"
      ></button>
      <button
        @click.prevent="onChangeColor($event, '#af9b8436')"
        :style="{ backgroundColor: '#af9b8436' }"
      ></button>
      <button
        @click.prevent="onChangeColor($event, '#fff')"
        :style="{ backgroundColor: '#fff' }"
      ></button>
    </div>
  </section>
</template>

<script>
export default {
  data() {
    return {
      isPaletteShown: false,
    };
  },
  methods: {
    openPalette() {
      this.isPaletteShown = !this.isPaletteShown;
    },
    onChangeColor(ev, color) {
      this.$emit("changeColor", color);
      this.isPaletteShown = false;
    },
  },
};
</script>

