<template>
  <div class="add-book-wraper">
    <h2>Enter book name</h2>
    <i class="fas fa-book-open"></i>

    <section class="add-book">
      <input
        ref="addbookinput"
        type="text"
        v-model="bookSearch"
        class="styled-input"
        @input="findBook"
        placeholder="Add book by name"
      />
      <div class="search-book-container">
        <ul class="google-books-list" v-if="bookSearch.trim().length > 3">
          <li
            class="google-book-title flex"
            v-for="(res, index) in searchRes"
            :key="index"
            @click="onAddBook($event, res)"
          >
            <p>{{ res.volumeInfo.title }}</p>
            <img
              v-if="res.volumeInfo.imageLinks"
              :src="res.volumeInfo.imageLinks.thumbnail"
              alt=""
            />
          </li>
        </ul>
      </div>
    </section>
  </div>
</template>

<script>
import { bookService } from "../services/bookService";
import eventBus from '../services/eventBus';
export default {
  data() {
    return {
      bookSearch: "",
      searchRes: [],
    };
  },
  mounted() {
    this.$refs.addbookinput.focus();
  },
  methods: {
    async findBook(ev) {
      if (ev.target.value.trim().length > 2) {
        this.searchRes = await bookService.getBooksApi(ev.target.value);
      } else this.searchRes = [];
    },
    onAddBook(ev, book) {
      this.$store.dispatch({ type: "addBook", book });
      this.$emit("bookAdded");
      this.searchRes = [];
      this.bookSearch = "";
    },
  },
};
</script>

