<template>
  <section
    v-if="msg"
    :class="[
      `user-msg`,
      { success: msg.type === 'success', error: msg.type === 'failure' },
    ]"
    v-show="msg.txt"
  >
    <p>{{ msg.txt }}</p>
  </section>
</template>

<script>
import eventBus from "@/services/eventBus.js";

export default {
  data() {
    return {
      msg: {
        txt: "",
        type: "",
      },
    };
  },
  created() {
    eventBus.$on("showMsg", (msg) => {
      this.msg = {
        txt: msg.txt,
        type: msg.type,
      };
        setTimeout(() => {
          this.msg = {
            txt: "",
            type: "",
          };
        }, 4000);
    });
  },
  destroyed() {
    eventBus.$off("showMsg");
  },
};
</script>
