<template>
  <div class="flex rating-avg">
    <i class="fa fa-star active" />
    <span class="flex justify-between ">
      <span>{{ countRating }} </span>
      <span class="votes-count" v-if="reviews.length > 0"
        >({{ reviews.length }} votes)
      </span>
      <span class="votes-count" v-else>(0)</span>
    </span>
  </div>
</template>



<script>
import { bookService } from "../services/bookService";
export default {
    props:["reviews"],
  computed: {
    countRating() {
      return bookService.sumReviewsAvg(this.reviews);
    },
  },
};
</script>
