<template>
  <div
    class="modal-screen flex align-center justify-center"
    @keyup.esc="closeModal"
    @mousedown="closeModal"
  >
    <div class="modal-container"  @mousedown.stop="">
      <slot name="button"></slot>
      <slot name="content" ></slot>
    </div>
  </div>
</template>

<script>
export default {
  props: ["isModalShown"],
  methods: {
    checkKey(ev) {
      if (ev.key === "Escape") {
        this.closeModal();
      }
    },
    closeModal() {
      this.$emit("closemodal");
    },
  },
};
</script>

