<template>
  <section>
    <div v-if="reviews.length" class="review-list flex column">
      <ReviewPreview
        v-for="review in reviews"
        :key="review.id"
        :review="review"
        @removeReview="removeReview"
      />
    </div>
    <p v-else>No reviews to display</p>
  </section>
</template>

<script>
import ReviewPreview from "./ReviewPreview.vue";
export default {
  props: ["reviews"],

  components: { ReviewPreview },
  methods: {
    removeReview(reviewId) {
      this.$emit("removeReview", reviewId);
    },
  },
};
</script>

