
import { bookService } from '../../services/bookService'


export default {
    state: {
        books: null,
    },
    getters: {
        booksToShow(state) {
            if (!state.books) return []
            return state.books
        }
    },
    mutations: {
        setBooks(state, { books }) {
            state.books = books
        },
        bookSaved(state, { book }) {
            state.books.unshift(book)
        }
    },
    actions: {
        async loadBooks({ commit }) {
            const books = await bookService.query()
            const booksCopy = JSON.parse(JSON.stringify(books))
            commit({ type: 'setBooks', books: booksCopy })

        },
        async addBook({ commit }, { book }) {
            book = await bookService.addGoogleBook(book)
            commit({ type: 'bookSaved', book })
        }
    },
    modules: {
    }
}
