<template>
  <router-link :to="`/book/${book.id}`">
    <section class="book" v-colorful :style="{ backgroundColor: bgc }">
      <BookImg :book="book" />
      <div class="book--preview-details flex flex1 column">
        <h3 class="book-title">{{ book.title }}</h3>
        <p class="authors">{{ book.authors.join(", ") || "Unknown" }}</p>

        <p
          :class="[
            'bold book-price',
            {
              red: book.listPrice.amount > 150,
              green: book.listPrice.amount > 0 && book.listPrice.amount < 20,
            },
          ]"
        >
          {{ getCurrency }}
          {{
            book.listPrice.amount !== 0
              ? book.listPrice.amount.toFixed(2)
              : "Not for sale"
          }}
        </p>
        <RatingAvg :reviews="book.reviews" />
        <ColorPicker @changeColor="changeColor" />
      </div>
    </section>
  </router-link>
</template>

<script>
import ColorPicker from "./ColorPicker.vue";
import { bookService } from "../services/bookService";
import { utilsService } from "../services/utilsService";
import BookImg from "./BookImg";
import RatingAvg from "./RatingAvg.vue";
export default {
  props: ["book"],
  data() {
    return {
      bgc: null,
    };
  },

  computed: {
    getCurrency() {
      if (this.book.listPrice.currencyCode) {
        return utilsService.findCurrency(this.book.listPrice.currencyCode);
      }
    },
    countRating() {
      return bookService.sumReviewsAvg(this.book.reviews);
    },
  },
  methods: {
    changeColor(color) {
      this.bgc = color;
    },
  },

  components: {
    BookImg,
    RatingAvg,
    ColorPicker,
  },
};
</script>

