<template>
  <form
    @submit.prevent="onAddReview"
    :class="[`flex column add-review-form`, { reviewopen: isAddReview }]"
  >
    <input
      type="text"
      ref="inputref"
      v-model="reviewToAdd.name"
      required
      class="styled-input"
      placeholder="Enter your name"
    />
    <div class="flex justify-between">
      <p>When did you read the book?</p>
      <input
        type="date"
        v-model="reviewToAdd.readAt"
        @input="setDate"
        required
      />
    </div>
    <textarea
      required
      placeholder="What do you think about the book?"
      type="text"
      class="styled-input"
      v-model="reviewToAdd.description"
      cols="30"
      rows="10"
    ></textarea>
    <div class="stars-rate flex">
      <i
        :class="[`fas fa-star star-5`, { active: reviewToAdd.rating === 5 }]"
        @click="setRating(5)"
      ></i>
      <i
        :class="[`fas fa-star star-4`, { active: reviewToAdd.rating >= 4 }]"
        @click="setRating(4)"
      ></i>
      <i
        :class="[`fas fa-star star-3`, { active: reviewToAdd.rating >= 3 }]"
        @click="setRating(3)"
      ></i>
      <i
        :class="[`fas fa-star star-2`, { active: reviewToAdd.rating >= 2 }]"
        @click="setRating(2)"
      ></i>
      <i
        :class="[`fas fa-star star-1`, { active: reviewToAdd.rating >= 1 }]"
        @click="setRating(1)"
      ></i>
    </div>
    <button class="primary-btn">Save</button>
  </form>
</template>

<script>
import eventBus from "../services/eventBus";
export default {
  props: ["isAddReview"],
  data() {
    return {
      reviewToAdd: {
        name: "",
        description: "",
        rating: null,
        readAt: null,
      },
    };
  },
  methods: {
    setDate(ev) {
      this.reviewToAdd.readAt = ev.target.value;
    },
    onAddReview() {
      if (!this.reviewToAdd.rating) {
        eventBus.$emit("showMsg", {
          txt: "You must rate the book",
          type: "failure",
        });
        return;
      }
      this.$emit("onAddReview", this.reviewToAdd);
      this.reviewToAdd = {
        name: "",
        description: "",
        rating: null,
      };
    },
    setRating(rating) {
      this.reviewToAdd.rating = rating;
    },
  },
  watch: {
    isAddReview(now, before) {
      if (now) this.$refs.inputref.focus();
    },
    reviewToAdd(now, old) {
    },
  },
};
</script>

