<template>
  <section class="review">
    <div class="review-details flex justify-between">
      <h3>{{ review.name || "Books Reader" }}</h3>
      <div class="flex column align-center">
        <ReviewRating :rating="review.rating" />
        <p>{{ readAt }}</p>
         <i class="fa fa-trash remove-review-btn" @click="onRemoveReview"/>
      </div>
    </div>
    <LongTxt :txt="review.description" class="review-content"/>
  </section>
</template>

<script>
import LongTxt from "./LongText.vue";
import ReviewRating from "./ReviewRating.vue";

export default {
  props: ["review"],
  methods: {
    onRemoveReview() {
      this.$emit("removeReview", this.review.id);
    },
  },
  computed: {
    readAt() {
      return this.review.readAt.split("-").reverse().join("/");
    },
  },
  components: {
    ReviewRating,
    LongTxt,
  },
};
</script>

