<template>
  <section class="accordion-group">
    <slot name="team"></slot>
    <slot name="about"></slot>
    <slot name="contact"></slot>
  </section>
</template>

<script>
export default {
  data() {
    return {
      items: {
        about: false,
        team: false,
        contact: false,
      },
    };
  },
  created() {
    this.$on("changeSlot", this.changeSlot);
  },
  methods: {
    changeSlot(currSlotClicked) {
      this.items[currSlotClicked] = !this.items[currSlotClicked];
    },
  },
};
</script>

