<template>
  <section
    @click="onSlotCliked"
    :class="{ slotOpen: this.$parent.items[this.$attrs.name] }"
  >
    <slot>
      <title></title>
      <slot name="slotheader"></slot>
      <transition
        name="expand"
        @enter="enter"
        @after-enter="afterEnter"
        @leave="leave"
      >
        <slot
          name="slotbody"
          v-if="this.$parent.items[this.$attrs.name]"
        ></slot>
      </transition>
    </slot>
  </section>
</template>

<script>
export default {
  methods: {
    onSlotCliked() {
      this.$parent.$emit("changeSlot", this.$attrs.name);
    },
    enter(el) {
      el.style.height = "auto";
      const height = getComputedStyle(el).height;
      el.style.height = 0;
       getComputedStyle(el)

      setTimeout(() => {
        el.style.height = height;
      });
    },
    afterEnter(el) {
      el.style.height = "auto";
    },
    leave(el) {
      el.style.height = getComputedStyle(el).height;
      getComputedStyle(el);
      setTimeout(() => {
        el.style.height = 0;
      });
    },
  },
};
</script>

